<template>
  <div class="bankcard-container">
    <div class="content">
      <div
        :class="{'item green':item.name==='中国工商银行','item blue':item.name==='中国建设银行'}  "
        v-for="(item,index) in data"
        :key="item.id"
        @click="itemSelect(index,item.id)"
      >
        <div class="top">
          <div class="left">
            <div class="title">中国工商银行</div>
            <div class="label">储蓄卡</div>
          </div>
          <div class="right" v-show="payIndex===index">
            <i class="iconfont iconchenggong"></i>
          </div>
        </div>
        <div class="bottom">
          <span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
          </span>
          <span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
          </span>
          <span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
          </span>
          <span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
            <span class="round"></span>
          </span>
          <span>
            <span>8834</span>
          </span>
        </div>
      </div>
      <div class="item add" @click="path('bankcardCreate')">
        <i class="iconfont iconjia"></i>
        <span>点击添加银行卡</span>
      </div>
    </div>
    <div class="footer">
      <button type="submit" v-show="state===0" @click.prevent="submitForm">确定</button>
      <button type="submit" v-show="state!==0" @click.prevent="remove">删除</button>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      state: 0,
      payIndex: 0,
      data: [
        {
          id: 1,
          name: "中国工商银行",
        },
        {
          id: 2,
          name: "中国建设银行",
        },
      ],
    };
  },
  watch: {},
  computed: {},
  methods: {
    remove() {
      console.log("删除");
    },
    itemSelect(index, id) {
      this.payIndex = index;
    },
    path(name) {
      this.$router.push({ name });
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.bankcard-container {
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  background-color: #ffffff;
  .item {
    width: 100%;
    height: 140px;
    padding: 15px;
    font-size: 15px;
    border-radius: 10px;
    margin-bottom: 20px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .top {
      display: flex;
      justify-content: space-between;
      .left {
        .title {
        }
        .label {
          margin-top: 6px;
          font-size: 10px;
        }
      }
      .right {
        i {
          font-size: 22px;
          color: #fff;
        }
      }
    }
    .bottom {
      display: flex;
      justify-content: space-around;
      font-size: 22px;
      > span {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        .round {
          display: inline-block;
          background-color: #fff;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          margin-right: 5px;
          &:last-of-type {
            margin-right: 0;
          }
        }
      }
    }
  }
  .add {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #e6e6e6;
    i {
      font-size: 55px;
      color: #999999;
    }
    span {
      color: #666666;
    }
  }
  .green {
    background-color: #0fc590;
  }
  .blue {
    background-color: #378dd0;
  }
}
</style>


